<template>
  <div class="app-container" id="alarmEvidence">
    <div class="topBox filter-container">

      <el-select v-model="listQuery.type1"  @change='changeSelect' multiple collapse-tags class="filter-item">
        <el-checkbox class="selectAll" v-model="checked" @change='selectAll'>全选</el-checkbox>
        <el-option v-for="item in adasDmsaAlarmTypeOptions" :selected="item.value==400" :key="item.value" :label="item.key" :value="item.value"></el-option>
      </el-select>
      <el-date-picker class="filter-item" v-model="seachTime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="选择时间日期" type="datetimerange" :picker-options="pickerOptions" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
      <el-cascader @change="removeValid()" v-model="listQuery.imeiCar" placeholder="按车辆搜索" :props="propsCar" :options="dataCar" :show-all-levels="false" clearable filterable collapse-tags>

      </el-cascader>

      <el-button type="primary" @click="getList" class="filter-item" icon="el-icon-search">搜索</el-button>
      <el-button type="primary" @click="goBack" class="filter-item">返回</el-button>

    </div>

    <div class="tableBox"    v-loading="listLoading">
      <div class="empty-text" v-if="!list.length">暂无数据</div>
      <div v-for="item in list" class="evidenceImgBox">
        <div @click="showEvidence(item)">
          <img :src="dealUrl(item.fileUrl)"></img>
          <div class="hoverLine">
            <div>{{item.vehiIdno}}</div>
            <div>{{alarmObj[item.alarmType.toString()]}}</div>
            <div>{{formatDate1(item.fileTime)}}</div>
          </div>
        </div>
      </div>
    </div>
    <!-- <div id="alarmMap">

        </div> -->
    <pagination v-show="total>0" :total="total" :page-sizes="[15,20,25,30]" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />
    <el-dialog title="报警详情" top="20vh" :close-on-click-modal="false" :visible.sync="dialogVisible">
      <div class="detail">
        <div class="detailBox">
          <div>车牌号：{{alarmDetail.vehiIdno}}</div>
          <div>设备号：{{alarmDetail.devIdno}}</div>
          <div>报警类型：{{alarmObj[alarmDetail.alarmType]}}</div>
          <div>时间：{{formatDate1( alarmDetail.fileTime)}}</div>
          <div>经纬度：{{alarmDetail.jingDu?(alarmDetail.jingDu/1000000+','+alarmDetail.weiDu/1000000):''}}</div>
          <div>地址：{{alarmDetail.position}}</div>
          <div><img :src="dealUrl(alarmDetail.fileUrl)" alt=""></div>
          <!-- <el-button type="primary" size="mini"> <a :href="alarmDetail.downloadUrl+'.zip'" :download="alarmDetail.devIdno+'-'+alarmObj[alarmDetail.alarmType]+'-'+formatDate1( alarmDetail.fileTime)+'.zip'">证据导出</a></el-button> -->

        </div>
        <div id="alarmMap">
        </div>
      </div>

    </el-dialog>
  </div>

</template>
<script>

import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//报警列表 延迟报警 报警处理 查询设备最近上线时间 设备报警列表
import {
  alarmListAdasDsm,
  performanceReportPhotoListSafe
} from "../../../api/alarmList";
import {
  teamAndCarAllBindList,
  teamAndGunList
} from "../../../api/realTimeMonitoring";
import { formatDate, Gps2Bds } from "../../../../src/common"; //格式化时间
import mapIcon from "../../../../static/images/mapicon.png";
import ADASIcon from "../../../../static/images/ADAS.png";
// import { formatDate, Gps2Bd, Gps2Bds } from "../../../../";



export default {
  components: { Pagination },

  data() {

    return {

      checked: false,

      list: [], //报警记录列表

      total: 0, //报警记录总条数
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 15,
        type: 1,
        status: -1,
        type1: [624],
        keyword: "", //模糊查询设备号
        imeiCar: [],
        imeiGun: []
      },

      indexMethod: 1, //序号

      dialogFormVisible: false, //弹窗显隐

      //报警类型对象
      alarmTypeOptions: [
        { key: "枪支偏离车辆", value: 1 },
        { key: "车辆偏移路线", value: 2 },
        { key: "拆卸报警", value: 3 },
        { key: "枪支网络异常", value: 4 },
        { key: "车辆信号丢失", value: 5 },
        { key: "同一辆车周围少于2支枪", value: 6 },
        { key: "未到规定时间内打卡", value: 7 },
        // { key: "枪支信号丢失", value: 8 },
        { key: "车辆道路变换", value: 9 },
        { key: "电量不足", value: 10 },
        { key: "自定义的超速", value: 11 },
        { key: "一键报警", value: 12 },
        { key: "车载定位系统推送的超速", value: 13 },
        { key: "未保留笼统的异常", value: 14 },
        { key: "摄像头损坏", value: 15 },
        { key: "摄像头无信号", value: 16 },
        { key: "ADAS和DSM报警", value: 17 },
      ],

      adasDmsaAlarmTypeOptions: [
        { key: "分神驾驶报警1级", value: 624 },
        { key: "分神驾驶报警2级", value: 625 },
        { key: "前车碰撞预警", value: 400 },
        { key: "道路偏离预警", value: 401 },
        { key: "行人检测预警", value: 402 },
        { key: "驾驶员遮挡或镜头偏离位置", value: 403 },
        { key: "不系安全带", value: 404 },
        { key: "疲劳驾驶报警1级", value: 618 },
        { key: "疲劳驾驶报警2级", value: 619 },

        { key: "接打电话报警1级", value: 620 },
        { key: "接打电话报警2级", value: 621 },
        { key: "抽烟报警1级", value: 622 },
        { key: "抽烟报警2级", value: 623 },

        { key: "驾驶员异常报警1级", value: 626 },
        { key: "驾驶员异常报警2级", value: 627 },
        { key: "驾驶员变更事件1级", value: 630 },
        { key: "驾驶员变更事件2级", value: 631 },
      ],
      //异常处理状态对象
      alarmStatusOptions: [
        { key: "所有异常", value: -1 },
        { key: "未处理异常", value: 0 },
        { key: "忽略异常", value: 1 },
        { key: "延迟处理异常", value: 2 },
        { key: "已处理异常", value: 3 }
      ],
      alarmObj: {
        400: '前车碰撞预警',
        401: '道路偏离预警',
        402: '行人检测预警',
        403: '驾驶员遮挡或镜头偏离位置',
        404: '不系安全带',
        618: '疲劳驾驶报警1级',
        619: '疲劳驾驶报警2级',
        620: '接打电话报警1级',
        621: '接打电话报警2级',
        622: '抽烟报警1级',
        623: '抽烟报警2级',
        624: '分神驾驶报警1级',
        625: '分神驾驶报警2级',
        626: '驾驶员异常报警1级',
        627: '驾驶员异常报警2级',
        630: '驾驶员变更事件1级',
        631: '驾驶员变更事件2级'
      },
      seachTime: '',
      seachTime1: this.formatDateTime(new Date()), //初始化搜索时间

      dialogVisible: false,
      propsCar: {
        expandTrigger: "hover",
        multiple: true
      },
      propsGun: {
        expandTrigger: "hover",
        multiple: true
      },
      markerStart: new BMap.Icon(ADASIcon, new BMap.Size(26, 41), {
        anchor: new BMap.Size(13, 42),
        imageOffset: new BMap.Size(-199, -139)
      }),
      map: undefined,
      dataCar: [], // 多设备车
      dataGun: [], // 多设备枪,
      imei2name: {}, // 设备的imei和对应的名字
      gunAlarmType: [1, 3, 4, 10, 12],
      carAlarmType: [2, 5, 6, 7, 9, 11, 13, 14, 15, 16, 17],
      carAlarmTypeVal: 'gun',
      alarmDetail: {},
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },

    };
  },


  // components: {
  //   SelectTree
  // },
  mounted() {

    this.getTeamAndCarList();

    // this.initMap();
    // this.getDevlist();
    // this.getSelect()
  },
  methods: {
    dealUrl(url){
    //   let newUrl=url;
    //   if(url){
    
    //   let index=url.indexOf(':6611');
    //   if(index!==-1){
    //   let newUrl=url.substring(index+5)
    //  return newUrl=location.origin+'/image'+newUrl
     
    //   }
    //   }else{
    //     return '';
    //   }
       let newUrl;
       let reg=/^http(s|):\/\/\S*?\//;
      if(url){
      let str=url.match(reg)
      if(str.length){
        str=str[0]
      }
    
     
       newUrl=url.substring(str.length)
   
     return newUrl=location.origin+'/image/'+newUrl
     
     
      }else{
        return '';
      }
    

    
     

    },
    goBack() {
      
      this.$router.push({name:'驾驶安全行为报警记录',params:{imeiCar:this.listQuery.imeiCar}});
    },

    selectAll() {
     this.listQuery.type1 = []
      if (this.checked) {
        this.adasDmsaAlarmTypeOptions.map((item) => {
          this.listQuery.type1.push(item.value)
        })
      } else {
        this.listQuery.type1 = []
      }
    },
    changeSelect(val) {
      if (val.length === this.adasDmsaAlarmTypeOptions.length) {
        this.checked = true
      } else {
        this.checked = false
      }
    },
    showEvidence(item) {
      this.alarmDetail = item;

      this.dialogVisible = true;

      this.$nextTick(() => { this.initMap(item) });
    },
    formatDate1(time) {
      return formatDate(time, 1)
    },

    //用于判断报警类型应该显示的是枪支列表还是车辆列表
    getGunOrCarType(val) {
      if (this.gunAlarmType.indexOf(val) !== -1) {
        this.carAlarmTypeVal = 'gun'

      } else if (this.carAlarmType.indexOf(val) !== -1) {
        this.carAlarmTypeVal = 'car'

      }




    },
    //获取设备最近上线时间
    removeValid() {
      // 选择设备后移除验证
      // this.$refs.mdForm.clearValidate(["imeiCar", "imeiGun"]);
    },
    //获取左侧大队和车辆tree请求
    getTeamAndCarList() {
      let that = this;
      teamAndCarAllBindList({}).then(res => {
        if (res.status == 0) {
          that.dataCar = this.formatCas(res.data, "serialNo", "licenseNo"); // 设置车下拉树的数据格式
          if (this.$route.query && this.$route.query.hasOwnProperty('devId')) {
            let query = this.$route.query
            this.listQuery.type1 = [Number(query.alarmType)];
            this.seachTime = [query.alarmTime, query.endTime]
            for (let i = 0; i < this.dataCar.length; i++) {
              for (let j = 0; j < this.dataCar[i].children.length; j++) {
                if (that.dataCar[i].children[j].value == query.devId) {
                  that.listQuery.imeiCar = [[that.dataCar[i].value, query.devId]];
                  this.getList();
                  break;
                }
              }


            }

          }
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    // 获取枪支及大队列表
    getTeamAndGunList() {
      let that = this;
      teamAndGunList({}).then(res => {

        if (res.status == 0) {
          that.dataGun = this.formatCas(res.data, "iMei", "code"); // 设置枪下拉树的数据格式
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    formatCas(data, imei, name) {

      // 格式化级联选择器数据 data是原始数据 imei是vaue值 name是label值
      let result = [],
        id = 0,
        _t = this;
      for (let i in data) {
        let childs = []; // 格式化子数组
        if (i == "报废车辆") { continue }
        if (i == "报废枪支") { continue }

        data[i].forEach(x => {
          //过滤掉没有绑定设备的车
          if (x[imei] !== "") {

            childs.push({
              value: x[imei],
              label: x[name],
            });
            _t.imei2name[x[imei]] = x[name]; // 将设备号与设备名组成一个数组
          }
        });
        result.push({
          value: `i${id++}`,
          label: i == "" ? "未分组" : i,
          children: childs
        });
      }
      return result;

    },
    getDevice() {
      this.listLoading = true;
      this.imei1 = this.imei;
      this.onlineTime = null;
      // gunOnline({ imei: this.imei }).then(res => {
      //   if (res && res.status == 0) {
      //     this.onlineTime = res.data;
      //   } else {
      //     this.$message.error(res.msg);
      //   }
      // });
      //获取设备报警列表
      checkDevice({ imei: this.imei1 }).then(response => {
        if (response.data == null) {
          this.list1 = [];
        } else {
          this.list1 = response.data;
        }

        this.listLoading = false;
      });
    },

    //导出
    exportExcel() {
      this.$message.warning("稍等,数据量较大,请不要重复点击哦");
      this.exportData.tableName =
        this.seachTime.split("_")[0] + "_" + this.seachTime.split("_")[1];
      let date = this.seachTime;

      this.exportData.days = this.seachTime.split("_")[2];
      this.exportData.expType = this.listQuery.type;
      this.exportData.expStatus = this.listQuery.status;
      this.exportData.keyword = this.listQuery.keyword;
      let type = this.exportData.expType;
      let status = this.exportData.expStatus;
      let keyword = this.exportData.keyword
      // console.warn(this.exportData)

      // return;
      exportBigExcel(this.exportData).then((response, a, b) => {
        let blob = new Blob([response], { type: "application/excel" }); // 假设文件为pdf
        let link = document.createElement("a");
        let typeText, statusText;
        for (let i = 0; i < this.alarmTypeOptions.length; i++) {
          if (type == this.alarmTypeOptions[i].value) {
            typeText = this.alarmTypeOptions[i].key
            break;
          }
        }
        for (let j = 0; j < this.alarmStatusOptions.length; j++) {
          if (status == this.alarmStatusOptions[j].value) {
            statusText = this.alarmStatusOptions[j].key;
            break;
          }
        }
        let fileName;
        if (keyword) {
          fileName = keyword + "----" + date + "----" + typeText + "----" + statusText + "----" + "报警记录" + ".xlsx";
        } else {
          fileName = date + "----" + typeText + "----" + statusText + "----" + "报警记录" + ".xlsx";
        }

        link.href = window.URL.createObjectURL(blob);
        link.download = fileName;
        link.click();
        link.remove();
      });
    },
    //格式化时间
    formatDateTime(date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      var d = date.getDate();
      // d = d < 10 ? "0" + d : d;
      var h = date.getHours();
      var minute = date.getMinutes();
      minute = minute < 10 ? "0" + minute : minute;
      return y + "_" + m + "_" + d;
    },
    //报警记录列表
    getList() {
      let obj = {}, arr = this.listQuery.imeiCar;
      obj.jsession = setInterval.jsession;
      obj.vehiIdno = [];
      for (let i = 0; i < arr.length; i++) {
        let code=this.$store.state.realTimeMonitor.carAndGunObj[arr[i][1]].code;
        obj.vehiIdno.push(arr[i][1]);
        obj.vehiIdno.push(code);
      }

      obj.begintime = this.seachTime[0];
      obj.endtime = this.seachTime[1];


      obj.alarmType = this.listQuery.type1;
      obj.mediaType = 0;//图片
      obj.currentPage = this.listQuery.page;
      obj.pageRecords = this.listQuery.limit;
      if (!this.seachTime.length || !obj.vehiIdno.length) {
        this.$message.warning('请选择搜索条件');
        return;

      }
      this.listLoading = true;
      performanceReportPhotoListSafe(obj).then(res => {
        this.list = res.infos ? res.infos : [];
        this.total = res.pagination ? res.pagination.totalRecords : 0;
        this.listLoading = false;
      }).finally(() => {
        this.listLoading = false;
      });
    },

    //重置
    resetTemp() {
      this.temp = {
        uuid: null,
        info: null
      };
    },
    //处理报警
    dealData() {
      var that = this;
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          alarmHandler({
            uuids: { [this.seachTime1]: [this.temp.uuid] },
            info: { handlerInfo: this.temp.info }
          }).then(() => {
            that.getList();
            that.dialogFormVisible = false;
            that.$notify({
              title: "Success",
              message: "处理成功",
              type: "success",
              duration: 2000
            });
          });
          this.dialogFormVisible = false;
        }
      });
    },
    //点击处理或延迟按钮
    handleUpdate(row, type) {
      this.resetTemp();
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
      //点击处理报警
      this.temp.uuid = row.uuid;
      if (type == "deal") {
        this.temp.timeStamp = new Date(row.createTime).getTime();
        this.dialogStatus = "deal";
      } else {
        //点击延迟报警
        this.temp.uuid = row.uuid;
        this.dialogStatus = "check";
      }

      this.dialogFormVisible = true;
    },
    //延迟报警
    delayData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          alarmDelay({ uuids: [this.temp.uuid], time: this.temp.time }).then(
            () => {
              this.getList();
              this.dialogFormVisible = false;
              this.$notify({
                title: "Success",
                message: "Update Successfully",
                type: "success",
                duration: 2000
              });
            }
          );
          this.dialogFormVisible = false;
        }
      });
    },
    formatTime1(time) {
      return formatDate(time, 1);
    },

    formatTime(time) {
      return renderTime(time);
    },
    //显示报警状态
    alarmStatus(status) {
      if (status == 0) {
        return "未处理";
      } else if (status == 1) {
        return "忽略";
      } else if (status == 2) {
        return "延迟处理";
      } else if (status == 3) {
        return "处理完成";
      }
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    // 通过人员详情查询枪支
    getNetworkByRoute() {
      return new Promise((resolve, reject) => {
        destinationByRoad({ routeId: this.roadId })
          .then(res => {

            resolve(res.data)
          }).catch(error => {

          })
          .finally(() => {

          });
      });

    },
    //函数功能：初始化地图
    initMap(item) {
      let that = this;
      this.map = new BMap.Map("alarmMap", { enableMapClick: false });

      //添加地图类型控件
      this.map.addControl(
        new BMap.MapTypeControl({
          mapTypes: [BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
        })
      );
      this.map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      if (item.jingDu) {
        this.map.clearOverlays();
        let lng = item.jingDu / 1000000, lat = item.weiDu / 1000000, arr = [];
        arr.push(new BMap.Point(lng, lat));

        let convertorData = Gps2Bds(arr);
        let Blng = convertorData[0].lng;
        let Blat = convertorData[0].lat;
        let newArr = new BMap.Point(lng, lat);

        let marker = new BMap.Marker(newArr, {
          icon: new BMap.Icon(ADASIcon, new BMap.Size(30, 30), {

          })
        });
        marker.addEventListener("click", function (e) {
          that.openInfo(item, e, opts);
        });

        this.map.centerAndZoom(newArr, 15);


        this.map.addOverlay(marker);
      } else {
        this.map.centerAndZoom("中国", 13); //初始化定位
      }
      // that.myRichMarkerDraged = false;
      // that.myRichMarker.hide();
      // //初始化信息框偏移，并且监听窗口是否改变并重新设置偏移
      // that.mql = window.matchMedia("(max-width: 480px)");
      // that.mqCallback(that.mql);
      // // 信息框样式设置
      // // $(".myRichMarker")
      // //   .parent()
      // //   .css({
      // //     "border-radius": "20px",
      // //     background: "rgba(255, 255, 255, 0.5)"
      // //   });
      // $(".myRichMarker")
      //   .parent()
      //   .css({
      //     "border-radius": "20px",
      //     background: "transparent"
      //   });
      // // 指定回调函数
      // that.mql.addListener(that.mqCallback);
    },
    //  openInfo(content, e) {
    //     let opts = {
